:root {
  --disabled-button-default-color: #bdbdbd;
}


.App {
  background-image: url(./blu_intel_pattern.png);
  height: 100vh;
  padding-top: 4em;
  text-align: center;

  p {
    background-color: white;
    border: 2px solid grey;
    border-bottom: 0;
    font-size: 20pt;
    margin: 0;
    margin-left: calc(50% - 6em);
    min-height: 2em;
    width: calc(12em - 4px);
  }

  .buttons {
    button {
      background-color: black;
      border: 2px solid gray;
      color: white;
      cursor: pointer;
      font-size: 20pt;
      min-width: 3em;
      min-height: 2em;

      &:disabled {
        background-color: var(--disabled-button-default-color);
        cursor: not-allowed;

        &:hover {
          background-color: var(--disabled-button-default-color);
        }
      }

      &:hover {
        background-color: #212121;
      }

      &:not(:first-child) {
        border-left: 0;
      }
    }

    div {
      white-space: nowrap;
    }

    div:not(:first-child) {
      button {
        border-top: 0;
      }
    }

    div:last-child {
      button {
        background-color: #1b5e20;
        color: #c8e6c9;
        width: 12em;

        &:hover {
          background-color: #2e7d32;
        }
      }
    }
  }
}